import React, { useState } from 'react';
import moment from 'moment';
import DatePicker from 'react-date-picker';
import { Layout, theme, Button, Space, Typography, Timeline } from 'antd';
import { AimOutlined, CalendarOutlined } from '@ant-design/icons';
import './App.module.css';
import 'react-date-picker/dist/DatePicker.css';
import 'react-calendar/dist/Calendar.css';

const { Header, Content, Sider } = Layout;
const { Text } = Typography;
const DATE_FORMATS = [
  "MM-DD-YYYY", "M-DD-YYYY", "MM-D-YYYY", "M-D-YYYY", "MM-DD-YY", "M-DD-YY", "MM-D-YY", "M-D-YY",
  "YYYY-MM-DD", "YYYY-M-DD", "YYYY-MM-D", "YYYY-M-D", "MM/DD/YYYY", "M/DD/YYYY", "MM/D/YYYY",
  "M/D/YYYY", "MM/DD/YY", "M/DD/YY", "MM/D/YY", "M/D/YY", "YYYY/MM/DD",
  "YYYY/M/DD", "YYYY/MM/D", "YYYY/M/D"
];

export interface IDate {
  start: string;
  end: string;
}

const App: React.FC = () => {
  const {
    token: { colorBgContainer },
  } = theme.useToken();

  const [dateList, setDateList] = useState<IDate[]>([]);
  //const [monthSelector, setMonthSelector] = useState<boolean>(false);

  const [startDate, setStartDate] = useState<any>('');
  const [endDate, setEndDate] = useState<any>('');


  const onFinish = () => {
    let start = moment(startDate, DATE_FORMATS, true).format('DD-MM-YYYY');
    let end = moment(endDate, DATE_FORMATS, true).format('DD-MM-YYYY');

    if (startDate && endDate) {
      setDateList((state: IDate[]) => {
        let values = [...state];
        values.push({ start, end });
        return values;
      });

      setStartDate('');
      setEndDate('');
    }
  };

  return (
    <Layout style={{ height: "100vh" }}>
      <Header className="header" >
        <div className="logo" />
      </Header>

      <Layout style={{ padding: '24px 0', background: colorBgContainer, height: "100%" }}>
        <Content style={{ padding: '0 24px' }}>
          <Space size={48}>
            <Space size="large">
              <Text strong>Start Date:</Text>
              <DatePicker
              className="ant-input"
                onChange={setStartDate}
                value={startDate}
                openCalendarOnFocus={false}
                dayPlaceholder="DD"
                monthPlaceholder="MM"
                yearPlaceholder="YYYY"
                calendarIcon={null}
                clearIcon={null}
                required />
            </Space>

            <Space size="large">
              <Text strong>End Date:&nbsp;&nbsp;</Text>
              <DatePicker
              className="ant-input"
                onChange={setEndDate}
                value={endDate}
                openCalendarOnFocus={false}
                dayPlaceholder="DD"
                monthPlaceholder="MM"
                yearPlaceholder="YYYY"
                calendarIcon={null}
                clearIcon={null}
                required />
            </Space>

            <Button type="primary" htmlType="submit" onClick={onFinish}>
              Add Dates
            </Button>
          </Space>

          <Layout hasSider style={{ padding: '24px', background: colorBgContainer, height: "100%", overflow: "auto" }}>
            <Sider theme="light" width="30vw" className="timelineSider">
              <Timeline mode="alternate">
                {
                  dateList.map((field: IDate, index: number) => ([
                    index > 0 ? (
                      <Timeline.Item dot={<AimOutlined />}>
                        <Space>
                          <Text>{moment(field.start, 'dd-MM-yyyy').diff(moment(dateList[index - 1].end, 'dd-MM-yyyy'), 'days')} days</Text>
                        </Space>
                      </Timeline.Item>
                    ) : null,
                    <Timeline.Item dot={<CalendarOutlined style={{ fontSize: '16px' }} />}>
                      <Space>
                        <Text strong>Start Date: </Text>
                        <Text>{field.start}</Text>
                      </Space>
                    </Timeline.Item>,
                    <Timeline.Item>
                      <Space>
                        <Text>{moment(field.end, 'dd-MM-yyyy').diff(moment(field.start, 'dd-MM-yyyy'), 'days')} days</Text>
                      </Space>
                    </Timeline.Item>,
                    <Timeline.Item dot={<CalendarOutlined style={{ fontSize: '16px' }} />}>
                      <Space>
                        <Text strong>End Date: </Text>
                        <Text>{field.end}</Text>
                      </Space>
                    </Timeline.Item>
                  ]))
                }
              </Timeline>
            </Sider>
            <Content>
              {
                dateList.map((field: IDate, index: number) => (
                  <Space key={index} style={{ display: 'flex', marginBottom: 8 }} align="baseline">
                    <Space>
                      <Text strong>Start Date: </Text>
                      <Text>{field.start}</Text>
                    </Space>
                    <Space>
                      <Text strong>End Date: </Text>
                      <Text>{field.end}</Text>
                    </Space>
                  </Space>
                ))
              }
            </Content>
          </Layout>


        </Content>
      </Layout>
    </Layout>
  );
};

export default App;